@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
/* Fonts */

/* Main */

:root {
  --primaryLightColor: #a8ced1;
  --primaryColor: #79cdd3;
  --primaryDarkColor: #c02c03;
  --primaryFont:'Quicksand', sans-serif;
  --slantedFont:'Quicksand', sans-serif;
  --mainWhite: #fff;
  --offWhite: #f7f7f7;
  --mainBackground: #f8f8f8;
  --mainOverlay: rgba(35, 10, 36, 0.4);
  --mainBlack: #222;
  --mainGrey: #ececec;
  --darkGrey: #afafaf;
  --mainRed: #bd0303;
  --mainTransition: all 0.3s linear;
  --mainSpacing: 0.3rem;
  --lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
  --mainBorderRadius: 0.25rem;
  --smallWidth: 85vw;
  --maxWidth: 40rem;
  --fullWidth: 1170px;
}

* {
  margin: auto;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family:'Quicksand', sans-serif;
  color: #222;
  background: #f8f8f8;
  line-height: 1.4;
  font-size: 1rem;
  font-weight: 300;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Quicksand', sans-serif;
  margin-bottom: 1.25rem;
  letter-spacing: 0.3rem;
}
p {
  margin-bottom: 1.25rem;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: #222;
}
img {
  width: 100%;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  display: block;
}
.loading {
  text-transform: capitalize;
  text-align: center;
  font-size: 1.7rem;
  margin-top: 3rem;
  letter-spacing: 0.3rem;
}
.nav-center h1 {
  margin-bottom: 0.2rem;
  font-style: oblique;
}
.loading img {
  width: 10rem;
  margin: 0 auto;
}

/* Buttons */

.btn,
.btn-white,
.btn-primary {
  text-transform: uppercase;
  letter-spacing: 0.3rem;
  color: #79cdd3;
  border: 2px solid #79cdd3;
  padding: 0.45rem 0.8rem;
  display: inline-block;
  transition: all 0.3s linear;
  transition: var(--mainTransition);
  cursor: pointer;
  font-size: 0.8rem;
  background: transparent;
  border-radius: 0.25rem;
  border-radius: var(--mainBorderRadius);
  display: inline-block;
}
.btn:hover {
  background: #79cdd3;
  color: #fff;
}
.btn-white {
  background: transparent;
  color: #fff;
  border-color: #fff;
}
.btn-white:hover {
  background: #fff;
  color: #79cdd3;
}
.btn-primary {
  background: #79cdd3;
  color: #fff;
  border-color: #79cdd3;
}
.btn-primary:hover {
  background: transparent;
  color: #79cdd3;
}
.btn-block {
  width: 100%;
  display: block;
  margin: 0 auto;
  box-shadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  box-shadow: var(--lightShadow);
  text-align: center;
}
.btn-details {
  padding: 0.25rem 0.4rem;
}
.btn-details:hover {
  background: transparent;
  color: #79cdd3;
}


/* Navbar */

.navbar {
  width: 100%;
  background: #fff;
  height: 96px;
  display: flex;
  align-items: center;
  border-bottom: 2px solid #79cdd3;
  box-shadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  box-shadow: var(--lightShadow);
}
.nav-center {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 85vw;
  width: var(--smallWidth);
  margin: 0 auto;
  max-width: 100%;
}
.nav-links {
  display: flex;
  align-items: flex-end;
  max-width: 40rem;

}
.nav-links a {
  text-transform: capitalize;
  display: inline-block;
  font-weight: bold;
  margin-right: 0.5rem;
  font-weight: 400;
  letter-spacing: 2px;
  font-size: 1.2rem;
  padding: 0.25rem 0.5rem;
}
.nav-links a:hover {
  color: #79cdd3;
}
.logo {
  padding-top: 25px;
  width: 12rem;
}

/* Hero and Banner */

.defaultHero {
  min-height: calc(40vh - 40px);
  background: url(/MiniBar-App/static/media/cocktailbaby.d6b4f7a8.jpg) center/cover no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
}

.banner {
  display: block;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  margin: 0 auto;
  padding: 2rem 1rem;
  text-align: center;
  text-transform: capitalize;
  letter-spacing: 0.3rem;
  letter-spacing: var(--mainSpacing);
}
.banner h1 {
  font-size: 2.5rem;
}
.banner div {
  width: 10rem;
  height: 2px;
  background: #79cdd3;
  margin: 1.7rem auto;
}
.banner p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

/* About */

.main-random {
  margin: 0 auto;
  text-align: center;
 
}
.main-random .cocktails-center{
  width: 85vw;
  width: var(--smallWidth);
  max-width: 40rem;
  max-width: var(--maxWidth);
  text-align: center;
  margin-left: 9.3rem;
 
}
.center {
  text-align: center;
  align-items: center;
  justify-content: center;
}

.about-section {
  width: 85vw;
  width: var(--smallWidth);
  max-width: 40rem;
  max-width: var(--maxWidth);
  margin: 0 auto;
  text-align: center;
  height: 82vh;
}
.about-section p {
  line-height: 2rem;
  font-weight: 400;
  letter-spacing: 2px;
}

/* Error */

.error-page {
  display: flex;
  justify-content: center;
}
.error-container {
  text-align: center;
  text-transform: capitalize;
}

/* Cocktails List */

.section {
  padding: 4rem 0;
}

.section-title {
  font-size: 30px;
  text-transform: capitalize;
  letter-spacing: 0.3rem;
  letter-spacing: var(--mainSpacing);
  text-align: center;
  margin-bottom: 3.5rem;
  margin-top: 1rem;
}
.cocktails-center {
  width: 85vw;
  width: var(--smallWidth);
  margin: 0 auto;
  max-width: 1170px;
  max-width: var(--fullWidth);
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-column-gap: 1.8rem;
  -webkit-column-gap: 1.8rem;
          column-gap: 1.8rem;
  grid-row-gap: 1.8rem;
  row-gap: 1.8rem;
}

/* Cocktail */

.cocktail {
  background: #fff;
  background: var(--mainWhite);
  margin-bottom: 2rem;
  box-shadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  box-shadow: var(--lightShadow);
  transition: all 0.3s linear;
  transition: var(--mainTransition);
  display: grid;
  grid-template-rows: auto 1fr;
  border-radius: 0.25rem;
  border-radius: var(--mainBorderRadius);

}
.cocktail:hover {
  box-shadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
  box-shadow: var(--darkShadow);
}
.cocktail img {
  border-top-left-radius: 0.25rem;
  border-top-left-radius: var(--mainBorderRadius);
  border-top-right-radius: 0.25rem;
  border-top-right-radius: var(--mainBorderRadius);
}
.cocktail-footer {
  padding: 1.5rem;
}
.cocktail-footer h3,
.cocktail-footer h4,
.cocktail-footer p {
  margin-bottom: 0.3rem;
}
.cocktail-footer h3 {
  font-size: 2.2rem;
}
.cocktail-footer p {
  color: #afafaf;
  color: var(--darkGrey);
  margin-bottom: 0.5rem;
}

.drink-list{
  list-style-type: circle;
}

/*  Form */

.form {
  width: 85vw;
  width: var(--smallWidth);
  margin: 0 auto;
  max-width: 40rem;
  max-width: var(--maxWidth);
}

.search-form {
  background: #fff;
  padding: 1.25rem 1rem;
  text-transform: capitalize;
  border-radius: 0.25rem;
  border-radius: var(--mainBorderRadius);
  box-shadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  box-shadow: var(--lightShadow);
}

.form-control label {
  display: block;
  margin-bottom: 0.5rem;
  color: #79cdd3;
}
.form-control input {
  width: 100%;
  border: none;
  border: 2px solid #afafaf;
  border: 2px solid var(--darkGrey);
  border-radius: 0.25rem;
  border-radius: var(--mainBorderRadius);
  padding: 0.5rem;
  font-size: 1.2rem;
  outline: none;
}

/* Cocktail */

.cocktail-section {
  text-align: center;
}

.drink {
  width: 85vw;
  width: var(--smallWidth);
  max-width: 1170px;
  max-width: var(--fullWidth);
  margin: 0 auto;
  text-align: left;
}
.drink img {
  border-radius: 0.25rem;
  border-radius: var(--mainBorderRadius);

}
.drink p {
  font-weight: bold;
  text-transform: capitalize;
}
.drink span {
  margin-right: 0.5rem;
}
.drink-info {
  padding-top: 2rem;
}

@media screen and (min-width: 576px) {
  .banner {
    padding: 2rem 3rem;
  }
  .banner h1 {
    font-size: 3rem;
  }
}
@media screen and (min-width: 992px) {
  .banner {
    padding: 2rem 6rem;
  }
  .banner h1 {
    font-size: 4rem;
  }
}

@media screen and (min-width: 992px) {
  .drink {
    display: grid;
    grid-template-columns: 2fr 3fr;
    grid-gap: 3rem;
    gap: 3rem;
    align-items: center;
  }
  .drink-info {
    padding-top: 0;
  }
  .main-random .cocktails-center{
    margin-left: 50px;
     
  }
}
@media screen and (min-width: 980px) {
  .root, .navbar .main-random .cocktails-center{
  margin: 0 auto;
     
  }}

  @media screen and (max-width: 900px) {
    .main-random .cocktails-center{
  margin: 0 auto;
       
    }
}
